 'Roboto', sans-serif;\n\n    li {\n      border-bottom: 2px solid lighten($color--primary, 10%);\n      display: flex;\n      align-items: center;\n      justify-content: space-between;\n      cursor: pointer;\n      transition: .4s;\n\n      &:first-child {\n        border-top: 2px solid lighten($color--primary, 10%);\n      }\n\n      .date {\n        display: flex;\n        align-items: center;\n        color: rgba(black, .25);\n        padding-right: 1rem;\n        font-size: .875rem;\n      }\n\n      .arrow {\n        width: 20px;\n        margin-left: 1rem;\n      }\n\n      .title {\n        width: 100%;\n        text-decoration: none;\n        color: black;\n        font-size: 1rem;\n        padding: 1.5rem 1rem;\n        letter-spacing: .5px;\n      }\n\n      @for $i from 4 to 9 {\n        &:nth-child(#{$i}) {\n          display: none;\n        }\n      }\n\n      &:hover {\n        background-color: $color--secondary;\n        border-bottom: 2px solid transparent;\n\n        &:first-child {\n          border-top: 2px solid transparent;\n        }\n      }\n\n      &.hideBorder {\n        border-bottom: 2px solid transparent;\n      }\n    }\n  }\n\n  &__btn {\n    align-self: flex-end;\n    color: black;\n    font-size: 1rem;\n    padding-bottom: .5rem;\n    border-bottom: 2px solid black;\n    text-decoration: none;\n    font-family: 'Roboto', sans-serif;\n    margin-top: 2rem;\n    transition: .3s;\n\n    &:hover {\n      color: $color--hover;\n      border-bottom: 2px solid $color--hover;\n    }\n  }\n\n  &__heading {\n    font-size: 1.75rem;\n    font-weight: 400;\n    padding-bottom: 1.25rem;\n  }\n}\n","$color--primary: #E6A8FF;\n$color--secondary: #F7F1E6;\n$color--hover: #B63CFF;\n$color--text: #465451;\n// #F7F1E6\n// #4CE0B3\n// #E55381\n// #FF9856\n// #CE53FF\n$theme1: (\n  primary: #C60F7B,\n  secondary: #D6D1CD,\n  bg: #E5ECE9\n);\n",".main {\n  &.theme1 {\n\n    &--1 {\n      background-color: map-get($theme1, bg);\n      color: #333;\n      border-top: 100px solid map-get($theme1, primary);\n\n      #theme1 {\n        .theme {\n          border: 1px solid map-get($theme1, secondary);\n        }\n      }\n    }\n  }\n}\n","@import 'theme1';\n\n.main {\n  background-color: #111;\n  width: 100vw;\n  height: 100vh;\n  padding: 1rem;\n  font-family: 'Roboto Mono', sans-serif;\n  color: #ccc;\n}\n\n.dialog-container {\n\n  .dialog {\n    display: block;\n  }\n}\n\n.theme-picker {\n  margin-top: 1rem;\n  opacity: 0;\n\n  &.active {\n    opacity: 1;\n  }\n\n  ul {\n    list-style: none;\n    display: flex;\n  }\n\n  li {\n    margin-right: 1rem;\n    cursor: pointer;\n    &:first-child {\n\n      .theme {\n        div:first-child {\n          background-color: #D6D1CD;\n        }\n\n        div:nth-child(2) {\n          background-color: #E5ECE9;\n        }\n\n        div:nth-child(3) {\n          background-color: #C60F7B;\n        }\n      }\n    }\n\n    &:nth-child(2) {\n      .theme {\n        div:nth-child(1) {\n          background-color: #8963BA;\n        }\n\n        div:nth-child(2) {\n          background-color: #AFE3C0;\n        }\n\n        div:nth-child(3) {\n          background-color: #90C290;\n        }\n      }\n    }\n  }\n\n  .theme {\n    display: flex;\n\n    div {\n      display: block;\n      width: 100px;\n      height: 50px;\n    }\n  }\n}\n","@keyframes pulse {\n  0% {\n    transform: scale(1);\n  }\n\n  50% {\n    transform: scale(1.1);\n  }\n}\n\n@keyframes movingClouds {\n  0% {\n    transform: translateX(-10vw);\n  }\n\n  100% {\n    transform: translateX(100vw);\n  }\n}\n",".container {\n  // margin-bottom: 10rem;\n  height: 100vh;\n}\n",".header {\n  display: flex;\n  justify-content: space-between;\n  align-items: flex-start;\n  padding: 1rem 0;\n\n  &__info {\n    cursor: pointer;\n\n    &:hover {\n\n      h1 {\n        color: $color--hover;\n      }\n    }\n\n    h1,\n    h3 {\n      font-family: 'Lato';\n    }\n\n    h1 {\n      margin-top: .5rem;\n      font-size: 1.25rem;\n      letter-spacing: .5px;\n      font-weight: 800;\n    }\n\n    h3 {\n      font-size: .75rem;\n      font-weight: 200;\n      letter-spacing: .5px;\n    }\n  }\n}\n",".nav {\n  font-family: 'Lato', sans-serif;\n  display: flex;\n\n  ul {\n    display: flex;\n    list-style: none;\n    font-size: .875rem;\n    padding-top: .5rem;\n\n    li:not(.social-media__btn) {\n      margin: 0 1rem;\n      cursor: pointer;\n\n      &:last-child {\n        margin-right: 0;\n      }\n\n      &:hover {\n        color: $color--primary;\n      }\n\n      &.active {\n        color: $color--primary;\n      }\n    }\n  }\n}\n",".tiles {\n  display: flex;\n  flex-wrap: wrap;\n  width: 100%;\n  justify-content: space-between;\n  margin-bottom: 5rem;\n  padding: 5%;\n}\n\n.tile {\n  width: 32.5%;\n  height: 300px;\n  background-color: $color--primary;\n  color: black;\n  padding: 3rem 3rem;\n  text-decoration: none;\n  margin: 0 1% 1% 0;\n  font-family: 'Roboto', sans-serif;\n  display: flex;\n  flex-direction: column;\n  transition: .3s ease-in-out;\n  overflow: hidden;\n\n  &:hover {\n\n    .tile__info {\n      transform: translateY(10%);\n    }\n\n    .tile__view-more {\n      transform: translateY(0);\n    }\n  }\n\n  &__info {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    transform: translateY(50%);\n    transition: .4s ease-in-out;\n  }\n\n  &__view-more {\n    text-decoration: none;\n    color: black;\n    padding: .75rem 1rem;\n    border: 2px solid black;\n    margin-top: 2rem;\n    align-self: center;\n    transform: translateY(200%);\n    transition: .4s ease-in-out;\n  }\n\n  &__label {\n    text-transform: uppercase;\n    font-size: .775rem;\n  }\n\n  &__heading {\n    font-weight: 400;\n    font-size: 2.5rem;\n  }\n\n  &__desc {\n    padding-top: .5rem;\n    text-align: center;\n    font-size: .875rem;\n  }\n\n  &.pagelink {\n    background-color: $color--secondary;\n    align-items: center;\n    justify-content: center;\n\n    &:hover {\n      background-color: $color--primary;\n    }\n  }\n\n  &.double {\n    width: 66.5%;\n  }\n\n  &.centered {\n    align-items: center;\n    justify-content: center;\n\n    .tile__label {\n      text-transform: capitalize;\n      font-size: 1rem;\n      display: none;\n    }\n  }\n\n  &:nth-child(2),\n  &:nth-child(5) {\n    margin-right: 0;\n  }\n\n  &:hover {\n    background-color: $color--secondary;\n  }\n}\n",".projects {\n  display: flex;\n  flex-direction: column;\n  margin: 5rem 0 7rem;\n  font-family: 'Roboto', sans-serif;\n  padding: 0 5%;\n\n  .section__heading {\n    margin-left: 1.5%;\n  }\n\n  div {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    flex-wrap: wrap;\n  }\n\n  .tile {\n    width: 23%;\n    height: 23%;\n    margin: 1%;\n    background-color: $color--secondary;\n\n    &:hover {\n      background-color: $color--primary;\n    }\n\n    h1 {\n      font-size: 1.7rem;\n    }\n\n    span {\n      font-size: 1rem;\n    }\n  }\n}\n",".footer {\n  height: 7rem;\n  background-color: $color--secondary;\n  width: 100%;\n  padding: 2rem;\n\n  h3 {\n    font-weight: 400;\n    font-size: .875rem;\n    margin-bottom: .25rem;\n  }\n\n  a {\n    color: $color--hover;\n    transition: .3s;\n\n    &:hover {\n      color: $color--primary;\n    }\n  }\n}\n",".social-media {\n  display: flex;\n  list-style: none;\n  align-items: center;\n  justify-content: flex-end;\n  margin-left: 2rem;\n\n  &__btn {\n    margin-right: .5rem;\n    transition: .4s ease-in-out;\n\n    &:last-child {\n      margin-right: 0;\n    }\n\n    svg {\n      width: 1.2rem;\n    }\n\n    &:hover {\n      opacity: .5;\n    }\n  }\n}\n",".intro {\n  height: 100vh;\n  display: flex;\n  flex-direction: column;\n  position: relative;\n  color: black;\n\n  .ill {\n    position: relative;\n    transition: 1s;\n    width: 100%;\n    position: absolute;\n    bottom: 0;\n    height: 100%;\n    background-image: linear-gradient(transparent 90%, $color--primary 0);\n\n    .building {\n      display: none;\n      width: 5%;\n      fill: $color--primary;\n      position: absolute;\n      bottom: 20px;\n      left: 65.8%;\n      z-index: 4;\n    }\n\n    .skyline {\n      width: 100%;\n      z-index: 1;\n      position: absolute;\n      bottom: 20px;\n      left: 270px;\n      // right: 0;\n      fill: $color--primary;\n    }\n\n    &.move {\n      transform: translateX(8px);\n    }\n  }\n\n\n\n  .triangle {\n    width: 50vw;\n    height: 40vh;\n    background-color: $color--primary;\n    clip-path: polygon(50% 21%, 0 100%, 100% 100%);\n    position: absolute;\n    bottom: 0;\n    left: -100px;\n    z-index: 1;\n    transition: .6s;\n\n    &.move {\n      transform: translateX(-5px);\n\n      &--left {\n        transform: translateX(10px);\n      }\n    }\n\n    &-right {\n      width: 70vw;\n      height: 60vh;\n      background-color: lighten($color--primary, 5%);\n      clip-path: polygon(50% 21%, 0 100%, 100% 100%);\n      position: absolute;\n      bottom: 0;\n      right: -100px;\n      z-index: 3;\n      transition: 1.2s;\n\n      &.move {\n        transform: translateX(-20px);\n\n        &--left {\n          transform: translateX(5px);\n        }\n      }\n    }\n  }\n\n  .cloud {\n    width: 10rem;\n    z-index: 2;\n    position: absolute;\n    bottom: 12rem;\n    transform: translateX(-100vw);\n\n    &--first {\n      animation: movingClouds 40s linear 1s infinite;\n      bottom: 10rem;\n    }\n\n    &--second {\n      animation: movingClouds 30s linear 2s infinite;\n    }\n\n    &--third {\n      animation: movingClouds 20s linear infinite;\n      bottom: 15rem;\n    }\n\n    path {\n      fill: rgba($color--secondary, 1);\n    }\n  }\n\n  h1, p {\n    padding-left: 1.5rem;\n    padding-right: 1rem;\n\n    @media screen and (min-width: 780px) {\n      padding-left: 8rem;\n    }\n  }\n\n  h1 {\n    margin-top: 3rem;\n    font-size: 4rem;\n    color: black;\n    font-family: 'Karla';\n    letter-spacing: -3px;\n